import React, {useState} from 'react';

import './ExpenseForm.css';

const ExpenseForm = (props) => {

    const cancelForm = () => {
        props.onCancelForm();
    }

    const [ enteredTitle , setEnteredTitle ] = useState('');
    const [ enteredAmount , setEnteredAmount ] = useState('');
    const [ enteredDate , setEnteredDate ] = useState('');

    const titleChangeHandler = (event) => {
        setEnteredTitle(event.target.value);
    }

    const amountChangeHandler = (event) => {
        setEnteredAmount(parseInt(event.target.value));
    }

    const dateChangeHandler = (event) => {
        setEnteredDate(event.target.value);
    }

    const submitHandler = (event) => {
        event.preventDefault();
        const expenseData = {
            title : enteredTitle,
            price : enteredAmount,
            date : new Date(enteredDate)
        }
        // console.log(expenseData);

        props.onSaveExpenseForm(expenseData);

        setEnteredTitle('');
        setEnteredAmount('');
        setEnteredDate('');
    }

    return (
        <form onSubmit={submitHandler}>
            <div className='new-expense__controls'>
                <div className='new-expense__control'>
                    <label>Title</label>
                    <input type='text' value={enteredTitle} onChange={ titleChangeHandler }/>
                </div>
                <div className='new-expense__control'>
                    <label>Amount</label>
                    <input type='number' min="0.1" step="0.1" value={enteredAmount} onChange={ amountChangeHandler }/>
                </div>
                <div className='new-expense__control'>
                    <label>Date</label>
                    <input type='date' min="2010-01-01" max="2022-12-31" value={enteredDate} onChange={ dateChangeHandler } />
                </div>
            </div>
            <div className='new-expense__actions'>
                <button type="cancel" onClick={ cancelForm }>Cancel</button>
                <button type="submit">Add Expense</button>
            </div>
        </form>
    )
}

export default ExpenseForm;